<template>
  <div class="chat">
    <v-card flat>
      <div class="chat-window" v-chat-scroll>
        <v-card hover raised tile v-for="message in messages" :key="message.id">
          <v-tooltip top>
            <template v-slot:activator="{ on }">
              <v-card-title v-on="on">
                <h5
                  class="headline font-weight-bold pr-2"
                  style="color:#cd00cd;"
                >
                  <{{ message.author }}>
                </h5>
                <h6
                  class="title font-weight-regular"
                  :class="{
                    'font-weight-bold': message.bold,
                    'font-italic': message.italic
                  }"
                  :style="{ color: message.color }"
                >
                  {{ message.text }}
                </h6>
              </v-card-title>
            </template>
            <span>{{ message.timestamp }}</span>
          </v-tooltip>
        </v-card>
      </div>
      <v-card-actions>
        <v-divider></v-divider>
        <NewMessage />
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import db from "@/firebase/init";
import moment from "moment";
import NewMessage from "@/views/components/NewMessage";

export default {
  name: "Chat",
  components: {
    NewMessage
  },
  data() {
    return {
      messages: []
    };
  },
  created() {
    let ref = db.collection("messages").orderBy("timestamp");

    ref.onSnapshot(snapshot => {
      snapshot.docChanges().forEach(change => {
        if (change.type == "added") {
          let doc = change.doc;
          this.messages.push({
            text: doc.data().text,
            bold: doc.data().bold,
            italic: doc.data().italic,
            color: doc.data().color,
            author: doc.data().author,
            id: doc.id,
            timestamp: moment(doc.data().timestamp).format("lll")
          });
        }
      });
    });
  }
};
</script>

<style>
.chat .chat-window {
  height: 53vh;
  word-break: break-all;
  overflow-y: scroll;
}
</style>
