<template>
  <div class="signup">
    <v-card flat>
      <v-form @submit.prevent="step++">
        <v-card-title class="title font-weight-regular justify-space-between">
          <span>{{ currentTitle }}</span>
          <v-avatar
            color="primary lighten-2"
            class="subheading white--text"
            size="24"
            v-text="step"
          ></v-avatar>
        </v-card-title>
        <v-window v-model="step">
          <v-window-item :value="1">
            <v-card-text>
              <v-text-field
                label="Department password"
                type="password"
                v-model="dPass"
                required
              ></v-text-field>
              <span class="caption grey--text text--darken-1">
                This is the department password needed for signup
              </span>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="2">
            <v-card-text>
              <v-text-field
                label="Email"
                v-model="email"
                required
              ></v-text-field>
              <v-text-field
                label="Username"
                v-model="username"
                required
              ></v-text-field>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="3">
            <v-card-text>
              <v-text-field
                label="Password"
                type="password"
                v-model="password"
                required
              ></v-text-field>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="4">
            <v-card-text>
              <div class="pa-3 text-xs-center">
                <h3 class="title font-weight-light mb-2">
                  Welcome to AOS Chat
                </h3>
                <span class="caption grey--text">Thanks for signing up!</span>
              </div>
            </v-card-text>
          </v-window-item>
        </v-window>
        <v-card-actions>
          <v-btn :disabled="step === 1 && step !== 4" flat @click="step--">
            Back
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            v-if="step !== 3"
            :disabled="step === 4"
            color="primary"
            depressed
            @click="step++"
          >
            Next
          </v-btn>
          <v-btn
            :loading="loading"
            v-if="step === 3"
            color="success"
            depressed
            @click="signup"
          >
            Submit
          </v-btn>
        </v-card-actions>
      </v-form>
      <v-alert :value="feedback" type="error">{{ feedback }}</v-alert>
    </v-card>
  </div>
</template>

<script>
import db from "@/firebase/init";
import firebase from "firebase";
import depPass from "@/auth/auth";

export default {
  name: "Signup",
  data() {
    return {
      step: 1,
      loading: false,
      username: null,
      email: null,
      password: null,
      dPass: null,
      feedback: null
    };
  },
  computed: {
    currentTitle() {
      switch (this.step) {
        case 1:
          return "Enter the department password";
        case 2:
          return "Set your email and username";
        case 3:
          return "Set your password";
        case 4:
          return "Account created";
      }
    }
  },
  methods: {
    signup() {
      if (this.dPass && this.password && this.email && this.username) {
        this.feedback = null;
        if (this.dPass === depPass) {
          this.feedback = null;
          this.loading = true;
          firebase
            .auth()
            .createUserWithEmailAndPassword(this.email, this.password)
            .then(cred => {
              // let user = firebase.auth().currentUser;
              cred.user
                .updateProfile({
                  displayName: this.username
                })
                .then(() => {
                  this.loading = false;
                  this.$router.push({ name: "Landing" });
                });
            })
            .catch(err => (this.feedback = err.message));
        } else {
          this.feedback = "Incorrect department password";
        }
      } else {
        this.feedback = "Please fill out all fields";
      }
    }
  }
};
</script>

<style>
.signup .spacer {
  cursor: default;
}
</style>