<template>
  <div class="navbar">
    <v-toolbar card dark color="#dd00dd">
      <v-toolbar-items>
        <v-btn to="/" v-ripple="{ class: 'purple--text' }" flat>
          <v-toolbar-title>AOS Chat</v-toolbar-title>
        </v-btn>
      </v-toolbar-items>
      <v-spacer></v-spacer>
      <v-toolbar-items v-if="user">
        <v-btn flat v-ripple="{ class: 'purple--text' }" class="title">
          Hello, {{ user.displayName }}
        </v-btn>
        <v-btn
          class="title"
          v-ripple="{ class: 'purple--text' }"
          flat
          @click="logout"
          >Logout</v-btn
        >
      </v-toolbar-items>
      <v-toolbar-items v-else>
        <v-btn
          to="/login"
          class="title"
          v-ripple="{ class: 'purple--text' }"
          flat
          >Login</v-btn
        >
        <v-btn
          to="/signup"
          class="title"
          v-ripple="{ class: 'purple--text' }"
          flat
          >Signup</v-btn
        >
      </v-toolbar-items>
    </v-toolbar>
  </div>
</template>

<script>
import firebase from "firebase";
import db from "@/firebase/init";

export default {
  name: "Navbar",
  data() {
    return {
      user: null
    };
  },
  methods: {
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => this.$router.push({ name: "Login" }));
    }
  },
  created() {
    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        this.user = user;
      } else {
        this.user = null;
      }
    });
  }
};
</script>

