<template>
  <div class="login">
    <v-card flat>
      <v-form @submit.prevent="login">
        <v-card-title class="title font-weight-regular justify-space-between">
          Login
        </v-card-title>
        <v-window>
          <v-window-item>
            <v-card-text>
              <v-text-field label="Email" v-model="email"></v-text-field>
              <v-text-field
                label="Password"
                v-model="password"
                type="password"
              ></v-text-field>
            </v-card-text>
            <v-card-actions>
              <v-btn :loading="loading" color="success" depressed type="submit">
                Login
              </v-btn>
            </v-card-actions>
          </v-window-item>
        </v-window>
      </v-form>
      <v-alert type="error" :value="feedback">{{ feedback }}</v-alert>
    </v-card>
  </div>
</template>

<script>
import db from "@/firebase/init";
import firebase from "firebase";

export default {
  name: "Login",
  data() {
    return {
      email: null,
      password: null,
      loading: false,
      feedback: null
    };
  },
  methods: {
    login() {
      if (this.email && this.password) {
        this.loading = true;
        firebase
          .auth()
          .signInWithEmailAndPassword(this.email, this.password)
          .then(() => {
            this.loading = false;
            this.$router.push({ name: "Landing" });
          })
          .catch(err => (this.feedback = err));
      } else {
        this.feedback = "Please enter both email and password";
      }
    }
  }
};
</script>

