<template>
  <div class="new-message">
    <v-divider class="py-1"></v-divider>
    <div class="px-3">
      <v-form @submit.prevent="addMessage">
        <v-text-field
          v-model="newMessage"
          :color="color"
          label="Message"
          autofocus
          clearable
          :class="{ 'font-weight-bold': bold, 'font-italic': italic }"
        >
        </v-text-field>

        <v-toolbar class="transparent" flat dense>
          <input class="pr-2" type="color" v-model="color" />
          <v-btn-toggle v-model="toggle_multiple" class="transparent" multiple>
            <v-btn flat v-model="bold">
              <v-icon>format_bold</v-icon>
            </v-btn>
            <v-btn v-model="italic" flat>
              <v-icon>format_italic</v-icon>
            </v-btn>
          </v-btn-toggle>
        </v-toolbar>
        <v-alert :value="errorFeedback" type="error"></v-alert>
      </v-form>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import db from "@/firebase/init";

// save most recently posted color as default for user
export default {
  data() {
    return {
      color: "#000000",
      newMessage: null,
      bold: false,
      italic: false,
      toggle_multiple: [],
      errorFeedback: null
    };
  },
  methods: {
    addMessage() {
      if (this.newMessage) {
        this.errorFeedback = null;
        let user = firebase.auth().currentUser;
        db.collection("messages")
          .add({
            text: this.newMessage,
            bold: this.bold,
            italic: this.italic,
            color: this.color,
            author: user.displayName,
            timestamp: Date.now()
          })
          .then(() => (this.newMessage = null));
      } else {
        this.errorFeedback = "You cannot send an empty message";
      }
    }
  }
};
</script>

<style>
.new-message {
  width: 100%;
}
</style>
